import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
let router = new VueRouter({
  mode: 'history',
  routes:[
    { path:'/'        , name:'Landing'   , component: ()=>import('./components/lander'   )},
    { path:'/contact' , name:'Contact'  , component: ()=>import('./components/contact'  )},
    // { path:'/test'    , name:'Contact'  , component: ()=>import('./components/yaml_test')},
    { path: '*'       , name: 'unknown' , component: ()=>import('./components/not_found')}
  ]
});
export default router;
